<template>
  <div>

      <v-dialog v-model="dialog" width="500">
        <v-card>
            <v-card-text>
                Datos ingresados
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" flat @click="dialog = false">Aceptar </v-btn>
            </v-card-actions>
        </v-card>
        </v-dialog>


    <v-form v-model="valid">
        <v-text-field v-model="anyo_ingreso" :rules="genericRules" label="Año de Ingreso:" required></v-text-field>
        <v-text-field v-model="anyo_egreso" :rules="genericRules" :counter="10" label="Año de Egreso:" required></v-text-field>
        <v-text-field v-model="fecha_examen" :rules="genericRules" :counter="10" label="Fecha Examen de Titulo:" required></v-text-field>
        <v-text-field v-model="titulo_tm" :rules="genericRules" :counter="30" label="Titulo Tesis o Memoria:" required></v-text-field>
        <v-text-field v-model="profesor" :rules="genericRules" :counter="30" label="Profesor Guía:" required></v-text-field>

        <br><br>
        <v-btn color="warning" fixed left round v-on:click="anterior" >Anterior</v-btn>
        <v-btn color="success" fixed right round v-on:click="finalizar" >Finalizar</v-btn>

    </v-form>

  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'Form3',
  data () {
    return {
      anyo_ingreso: '',
      anyo_egreso: '',
      fecha_examen: '',
      titulo_tm: '',
      profesor: '',
      drawer: null,
      valid: false,
      date: null,
      menu: false,
      dialog: false,
      genericRules: [
        v => !!v || 'Este campo es requerido',
      ]
    }
  },
  watch: {
    menu (val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
    }
  },
  methods: {
      anterior: function(){

          this.$router.replace('/form2')
      },
      finalizar: function(){

          //this.$router.replace('/form3')
          /*
          console.log('\n-----------------FORM1:')
          console.log(this.$rut)
          console.log(this.$nombres)
          console.log(this.$apellidos)
          console.log(this.$nacimiento)
          console.log(this.$titulo_acad)
          
          console.log('\n\n-----------------FORM2:')
          console.log(this.$email)
          console.log(this.$fono_movil)
          console.log(this.$fono_fijo)
          console.log(this.$facebook)
          console.log(this.$direccion)
          
          console.log('\n\n-----------------FORM3:')
          console.log(this.anyo_ingreso)
          console.log(this.anyo_egreso)
          console.log(this.fecha_examen)
          console.log(this.titulo_tm)
          console.log(this.profesor)
          */

        
        var urlKeeps = 'http://'+this.$ip_servidor+':8000/api/egresado';
          axios.post(urlKeeps, {

                id_usuario: parseInt(this.$id_usuario),

                //Datos de contacto
                /*
                fono_movil: parseInt(this.$fono_movil),
                fono_fijo: parseInt(this.$fono_fijo),
                email: this.$email,
                facebook: this.$facebook,
                direccion: this.$direccion,
                */

                //Datos
                titulo: this.$titulo_acad,
                anyo_ingreso: parseInt(this.anyo_ingreso),
                anyo_egreso: parseInt(this.anyo_egreso),
                fecha_examen: this.fecha_examen,
                titulo_tm: this.titulo_tm,
                profesor: this.profesor,
            },
            {
                headers: { 'Authorization': 'Bearer '+this.$token }
            }
            ).then(response => {
                console.log(response.data)
                this.dialog = true
            }).catch(error => {
                this.errors = 'No se pudo crear su servicio';
                this.error = "hay un error carajooo"
            });


      },
      save (date) {
        this.$refs.menu.save(date)
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



