<template>
  <div>
    
    <v-form v-model="valid">
        <v-text-field v-model="email" :rules="emailRules" label="Email Personal:" required></v-text-field>
        <v-text-field v-model="fono_movil" :rules="genericRules" :counter="10" label="Nº Teléfono Móvil:" required></v-text-field>
        <v-text-field v-model="fono_fijo" :rules="genericRules" :counter="10" label="Nº Teléfono Fijo:" required></v-text-field>
        <v-text-field v-model="facebook" :rules="genericRules" :counter="30" label="Facebook:" required></v-text-field>
        <v-text-field v-model="direccion" :rules="genericRules" :counter="30" label="Dirección Actual:" required></v-text-field>

        <br><br>
        <v-btn color="warning" fixed left round v-on:click="anterior" >Anterior</v-btn>
        <v-btn color="primary" fixed right round v-on:click="siguiente" >Siguiente</v-btn>

    </v-form>

  </div>
</template>

<script>
import Vue from 'vue'

export default {
  name: 'Form2',
  data () {
    return {
      email: '',
      fono_movil: '',
      fono_fijo: '',
      facebook: '',
      direccion: '',
      drawer: null,
      valid: false,
      date: null,
      menu: false,
      genericRules: [
        v => !!v || 'Este campo es requerido',
      ],
      emailRules: [
        v => !!v || 'el campo "E-mail" es requerido',
        v => /.+@.+/.test(v) || 'El E-mail debe ser válido'
      ]
    }
  },
  watch: {
    menu (val) {
      val && this.$nextTick(() => (this.$refs.picker.activePicker = 'YEAR'))
    }
  },
  methods: {
      anterior: function(){

          this.respalda()
          this.$router.replace('/form1')
      },
      siguiente: function(){

          this.respalda()
          this.$router.replace('/form3')
      },
      respalda: function(){

            //Se asignan los valores del formulario a las variables globales
            Vue.prototype.$email = this.email
            Vue.prototype.$fono_movil = this.fono_movil
            Vue.prototype.$fono_fijo = this.fono_fijo
            Vue.prototype.$facebook = this.facebook
            Vue.prototype.$direccion = this.direccion
      },
      save (date) {
        this.$refs.menu.save(date)
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



